"use client"

import { DashboardNav } from "@/components/dashboard-nav"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { TimePicker } from "@/components/ui/time-picker"
import { Clock, Truck, CheckCircle2, MapPin, Package, Eye } from "lucide-react"
import { TransportMap } from "@/components/transport-map"
import { withAuth } from "@/components/with-auth"
import { useState, useEffect } from "react"
import { toast } from "sonner"

type TransportItem = {
  id: string
  pickupRequestId: string
  transportType: string
  provider?: string
  assignedTo?: string
  pickupDate: string
  pickupWindow: {
    from: string
    to: string
  }
  cost?: number
  trackingLink?: string
  notes?: string
  status: string
  destination?: string
  quantity?: number
  wasteType?: string
  requesterId?: string
  pickupRequest?: {
    itemName: string
    quantity: number
    requesterName: string
  }
  assignedToName?: string
}

function TransportationPage() {
  const [transports, setTransports] = useState<TransportItem[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [formData, setFormData] = useState({
    requesterId: "",
    wasteType: "",
    quantity: "",
    requesterName: "",
    pickupMethod: "staff" as "staff" | "courier",
    assignedTo: "",
    pickupDate: "",
    pickupWindowFrom: "",
    pickupWindowTo: "",
    status: "scheduled" as "scheduled" | "delivered" | "pending",
    notes: "",
  })

  const mockTransports = [
    {
      id: "T-014",
      cafe: "Green Caf√©",
      destination: "EcoFarm",
      type: "Coffee Grounds",
      quantity: 15.2,
      status: "Scheduled",
      date: "2025-01-10",
      time: "10:00 AM",
    },
    {
      id: "T-015",
      cafe: "BrewHub",
      destination: "FoodBank NGO",
      type: "Food Waste",
      quantity: 22.0,
      status: "In Transit",
      date: "2025-01-09",
      time: "2:30 PM",
    },
    {
      id: "T-016",
      cafe: "Green Caf√©",
      destination: "Urban Farm Co.",
      type: "Organic Waste",
      quantity: 18.5,
      status: "Completed",
      date: "2025-01-08",
      time: "9:00 AM",
    },
    {
      id: "T-017",
      cafe: "Coffee Corner",
      destination: "Community Garden",
      type: "Coffee Grounds",
      quantity: 12.3,
      status: "Scheduled",
      date: "2025-01-11",
      time: "11:30 AM",
    },
    {
      id: "T-018",
      cafe: "Daily Brew",
      destination: "Local Farm",
      type: "Food Waste",
      quantity: 25.8,
      status: "In Transit",
      date: "2025-01-09",
      time: "3:45 PM",
    },
    {
      id: "T-019",
      cafe: "Green Caf√©",
      destination: "EcoFarm",
      type: "Plate Waste",
      quantity: 14.2,
      status: "Completed",
      date: "2025-01-07",
      time: "1:00 PM",
    },
  ]

  // Filter to only show scheduled pickups (pickup-scheduled or scheduled status)
  // Transportation page should only show scheduled pickups from pickup requests
  const displayTransports = transports.filter((t) => {
    const status = t.status.toLowerCase()
    return status === "scheduled" || status === "pickup-scheduled" || status === "completed"
  })

  const scheduledCount = displayTransports.filter(
    (t) => {
      const status = t.status.toLowerCase()
      return status === "scheduled" || status === "pickup-scheduled"
    }
  ).length
  const inTransitCount = displayTransports.filter(
    (t) => t.status === "in-transit" || t.status === "In Transit"
  ).length
  const completedCount = displayTransports.filter(
    (t) => t.status === "completed" || t.status === "Completed"
  ).length

  const getStatusColor = (status: string) => {
    const normalized = status.toLowerCase()
    if (normalized === "scheduled" || normalized === "pickup-scheduled") {
      return "bg-yellow-500/10 text-yellow-600 border-yellow-500/20"
    }
    if (normalized === "in-transit" || normalized === "in transit") {
      return "bg-blue-500/10 text-blue-600 border-blue-500/20"
    }
    if (normalized === "completed") {
      return "bg-primary/10 text-primary border-primary/20"
    }
    return "bg-muted text-muted-foreground"
  }

  const getStatusIcon = (status: string) => {
    const normalized = status.toLowerCase()
    if (normalized === "scheduled" || normalized === "pickup-scheduled") {
      return <Clock className="w-4 h-4" />
    }
    if (normalized === "in-transit" || normalized === "in transit") {
      return <Truck className="w-4 h-4" />
    }
    if (normalized === "completed") {
      return <CheckCircle2 className="w-4 h-4" />
    }
    return null
  }

  // Fetch transports
  useEffect(() => {
    fetchTransports()
  }, [])

  const fetchTransports = async () => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) return

      const response = await fetch("/api/transportation", {
        headers: { "x-user-id": storedUser.userId },
      })

      if (response.ok) {
        const data = await response.json()
        // Transform the data to match TransportItem type
        const transformedTransports = (data.transports || []).map((t: any) => {
          // Parse waste type from notes if available (for manually created records)
          let wasteType = "Unknown"
          if (t.notes) {
            const wasteTypeMatch = t.notes.match(/Waste Type:\s*(.+)/i)
            if (wasteTypeMatch) {
              wasteType = wasteTypeMatch[1].split("\n")[0].trim()
            }
          }

          // Parse requester ID from notes if available (for manually created records)
          let requesterId = t.id // Default to transport ID
          if (t.notes) {
            const requesterIdMatch = t.notes.match(/Requester ID:\s*(.+)/i)
            if (requesterIdMatch) {
              requesterId = requesterIdMatch[1].split("\n")[0].trim()
            }
          }

          // Get quantity - prefer from transport record, fallback to pickup request
          const quantity = t.quantity || t.pickupRequest?.wasteEntry?.quantity || 0

          // Get destination - prefer from transport record, fallback to pickup request requester
          const destination = t.destination || t.pickupRequest?.requester?.name || "Unknown"

          return {
            id: t.id,
            pickupRequestId: t.pickupRequestId || "",
            transportType: t.pickupRequest ? "internal" : "external",
            provider: t.provider || undefined,
            assignedTo: t.assignedTo?.name || t.assignedToName || undefined,
            pickupDate: t.scheduledDate || "",
            pickupWindow: {
              from: t.scheduledTime?.split("-")[0] || "",
              to: t.scheduledTime?.split("-")[1] || "",
            },
            cost: t.cost || undefined,
            trackingLink: t.trackingLink || undefined,
            notes: t.notes || undefined,
            status: t.status || "scheduled",
            destination: destination,
            quantity: quantity,
            wasteType: wasteType,
            requesterId: requesterId,
            pickupRequest: t.pickupRequest ? {
              itemName: t.pickupRequest.wasteEntry?.subType || wasteType,
              quantity: t.pickupRequest.wasteEntry?.quantity || quantity,
              requesterName: t.pickupRequest.requester?.name || destination,
            } : undefined,
            assignedToName: t.assignedTo?.name || undefined,
          }
        })
        setTransports(transformedTransports)
      }
    } catch (error) {
      console.error("Error fetching transports:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const handleCancel = () => {
    // Reset form data
    setFormData({
      requesterId: "",
      wasteType: "",
      quantity: "",
      requesterName: "",
      pickupMethod: "staff",
      assignedTo: "",
      pickupDate: "",
      pickupWindowFrom: "",
      pickupWindowTo: "",
      status: "scheduled",
      notes: "",
    })
    // Close dialog
    setIsDialogOpen(false)
  }

  const handleSchedulePickup = async () => {
    // Validate required fields
    if (!formData.requesterId || !formData.wasteType || !formData.quantity) {
      toast.error("Please fill in Requester ID, Type, and Quantity")
      return
    }

    if (!formData.requesterName) {
      toast.error("Please enter requester name")
      return
    }

    if (!formData.pickupDate || !formData.pickupWindowFrom || !formData.pickupWindowTo || !formData.assignedTo) {
      toast.error("Please fill in all required fields")
      return
    }

    // Validate quantity is a positive number
    const quantityNum = parseFloat(formData.quantity)
    if (isNaN(quantityNum) || quantityNum <= 0) {
      toast.error("Quantity must be a positive number")
      return
    }

    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("Please log in again")
        return
      }

      // Get businessId from localStorage or any waste entry
      let businessId = ""
      const storedUserData = JSON.parse(localStorage.getItem("user") || "{}")
      businessId = storedUserData.businessId || storedUserData.cafeId || ""
      
      // Fallback: Get businessId from any waste entry (user's business)
      if (!businessId) {
        try {
          const anyWasteResponse = await fetch("/api/waste?limit=1", {
            headers: { "x-user-id": storedUser.userId },
          })
          if (anyWasteResponse.ok) {
            const anyWasteData = await anyWasteResponse.json()
            if (anyWasteData.entries && anyWasteData.entries.length > 0) {
              businessId = anyWasteData.entries[0].cafeId || ""
            }
          }
        } catch (error) {
          console.error("Error fetching any waste entry for businessId:", error)
        }
      }
      
      if (!businessId) {
        toast.error("Could not determine business. Please ensure you have waste entries logged.")
        return
      }

      const destination = formData.requesterName
      const quantity = quantityNum

      // Format scheduled time as "HH:mm-HH:mm"
      const scheduledTime = `${formData.pickupWindowFrom}-${formData.pickupWindowTo}`

      // Determine status for transportation record
      // Note: TransportStatus enum only has SCHEDULED, IN_TRANSIT, COMPLETED, CANCELLED
      // So "pending" maps to SCHEDULED, "scheduled" maps to SCHEDULED, "delivered" maps to COMPLETED
      let transportStatus = "SCHEDULED"
      if (formData.status === "delivered") {
        transportStatus = "COMPLETED"
      } else {
        // Both "pending" and "scheduled" map to SCHEDULED
        transportStatus = "SCHEDULED"
      }

      // Build notes with all information
      const transportNotes = [
        `Requester ID: ${formData.requesterId}`,
        `Waste Type: ${formData.wasteType}`,
        formData.notes,
        `Pickup Method: ${formData.pickupMethod === "staff" ? "Caf√© staff" : "External courier (Grab / Lalamove)"}`,
        `Assigned to: ${formData.assignedTo}`,
      ]
        .filter(Boolean)
        .join("\n")

      // Create transportation record directly (no pickup request needed for manual entry)
      const response = await fetch("/api/transportation", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          destination: destination,
          quantity: quantity,
          scheduledDate: formData.pickupDate,
          scheduledTime: scheduledTime,
          notes: transportNotes || undefined,
          assignedToId: undefined, // We're using name string instead of user ID
        }),
      })

      if (response.ok) {
        const createdTransport = await response.json()
        
        // Update status if delivered (pending and scheduled both use SCHEDULED)
        if (formData.status === "delivered") {
          await fetch(`/api/transportation/${createdTransport.id}`, {
            method: "PATCH",
            headers: {
              "Content-Type": "application/json",
              "x-user-id": storedUser.userId,
            },
            body: JSON.stringify({
              status: "COMPLETED",
            }),
          })
        }

        toast.success("Pickup scheduled successfully")
        setIsDialogOpen(false)
        // Reset form
        setFormData({
          requesterId: "",
          wasteType: "",
          quantity: "",
          requesterName: "",
          pickupMethod: "staff",
          assignedTo: "",
          pickupDate: "",
          pickupWindowFrom: "",
          pickupWindowTo: "",
          status: "scheduled",
          notes: "",
        })
        // Refresh data without page reload
        fetchTransports()
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to schedule pickup")
      }
    } catch (error) {
      console.error("Error scheduling pickup:", error)
      toast.error("An error occurred. Please try again.")
    }
  }

  const handleMarkCompleted = async (transportId: string, pickupRequestId: string) => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("User not found. Please log in again")
        return
      }

      const response = await fetch(`/api/transportation/${transportId}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          status: "COMPLETED",
        }),
      })

      if (response.ok) {
        toast.success("Pickup marked as completed")
        // Refresh data without page reload
        fetchTransports()
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to mark as completed")
      }
    } catch (error) {
      console.error("Error marking as completed:", error)
      toast.error("An error occurred. Please try again.")
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <DashboardNav />

      <main className="pt-20 px-4 pb-8">
        <div className="container mx-auto max-w-7xl">
          {/* Header */}
          <div className="mb-6 flex items-start gap-3">
            <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center flex-shrink-0">
              <Truck className="w-6 h-6 text-primary" />
            </div>
            <div>
              <h1 className="font-display font-bold text-3xl text-foreground mb-1">Transportation Organizer</h1>
              <p className="text-muted-foreground">Manage food waste pickups and deliveries</p>
            </div>
          </div>

          {/* Overview Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-yellow-50 to-background border-2 border-yellow-500/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-yellow-500/10 flex items-center justify-center">
                  <Clock className="w-6 h-6 text-yellow-600" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{scheduledCount}</div>
              <div className="text-sm text-muted-foreground font-medium">Scheduled Pickups</div>
              <div className="text-xs text-muted-foreground mt-1">Upcoming waste collections</div>
            </Card>

            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-blue-50 to-background border-2 border-blue-500/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-blue-500/10 flex items-center justify-center">
                  <Truck className="w-6 h-6 text-blue-600" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{inTransitCount}</div>
              <div className="text-sm text-muted-foreground font-medium">In Transit</div>
              <div className="text-xs text-muted-foreground mt-1">Active deliveries in progress</div>
            </Card>

            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-green-50 to-background border-2 border-primary/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center">
                  <CheckCircle2 className="w-6 h-6 text-primary" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{completedCount}</div>
              <div className="text-sm text-muted-foreground font-medium">Completed Deliveries</div>
              <div className="text-xs text-muted-foreground mt-1">Total finished transports</div>
            </Card>
          </div>

          {/* Action Buttons */}
          <div className="flex flex-wrap gap-3 mb-6">
            <Button
              className="bg-primary hover:bg-primary/90 text-primary-foreground"
              onClick={() => {
                setIsDialogOpen(true)
              }}
            >
              <Truck className="w-4 h-4 mr-2" />
              Schedule Pickup
            </Button>
            <Button variant="outline" className="border-primary/20 hover:bg-primary/5 bg-transparent">
              Update Status
            </Button>
          </div>

          <div className="grid lg:grid-cols-3 gap-6 mb-8">
            {/* Transport Table */}
            <Card className="p-6 bg-card lg:col-span-2">
              <div className="flex items-center justify-between mb-6">
                <h3 className="font-display font-bold text-xl text-foreground">Transport Requests</h3>
                <Button variant="outline" size="sm">
                  Export
                </Button>
              </div>

              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b border-border bg-muted/50">
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Requester ID
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Caf√©
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Destination
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Type
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Quantity
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Status
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {displayTransports.map((transport) => {
                      // Use direct fields from transport, fallback to pickupRequest
                      const destination = transport.destination || transport.pickupRequest?.requesterName || "Unknown"
                      const itemType = transport.wasteType || transport.pickupRequest?.itemName || "Unknown"
                      const quantity = transport.quantity || transport.pickupRequest?.quantity || 0
                      const cafe = "Green Caf√©" // Could be fetched from waste data

                      return (
                        <tr key={transport.id} className="border-b border-border hover:bg-accent/5 transition-colors">
                          <td className="py-4 px-4 font-medium text-foreground">{transport.requesterId || transport.id}</td>
                          <td className="py-4 px-4">
                            <div className="flex items-center gap-2">
                              <div className="w-8 h-8 rounded-full bg-primary/10 flex items-center justify-center">
                                <Package className="w-4 h-4 text-primary" />
                              </div>
                              <span className="text-foreground">{cafe}</span>
                            </div>
                          </td>
                          <td className="py-4 px-4">
                            <div className="flex items-center gap-2">
                              <MapPin className="w-4 h-4 text-muted-foreground" />
                              <span className="text-foreground">{destination}</span>
                            </div>
                          </td>
                          <td className="py-4 px-4 text-muted-foreground">{itemType}</td>
                          <td className="py-4 px-4 font-medium text-foreground">{quantity} kg</td>
                          <td className="py-4 px-4">
                            <Badge variant="outline" className={getStatusColor(transport.status)}>
                              <span className="flex items-center gap-1.5">
                                {getStatusIcon(transport.status)}
                                {transport.status}
                              </span>
                            </Badge>
                          </td>
                          <td className="py-4 px-4">
                            {(transport.status === "pickup-scheduled" || transport.status === "scheduled" || transport.status === "Scheduled") ? (
                              <Button
                                variant="outline"
                                size="sm"
                                onClick={() => handleMarkCompleted(transport.id, transport.pickupRequestId)}
                                className="hover:bg-green-500/10 hover:text-green-600"
                              >
                                <CheckCircle2 className="w-4 h-4 mr-1" />
                                Mark as Completed
                              </Button>
                            ) : (
                              <Button variant="ghost" size="sm" className="hover:bg-primary/10 hover:text-primary">
                                <Eye className="w-4 h-4 mr-1" />
                                View
                              </Button>
                            )}
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </Card>

            {/* Transport Map */}
            <Card className="p-6 bg-card">
              <h3 className="font-display font-bold text-xl text-foreground mb-6">Route Tracking</h3>
              <TransportMap />
              <div className="mt-6 space-y-3">
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-primary animate-pulse" />
                    <span className="text-sm font-medium text-foreground">Active Routes</span>
                  </div>
                  <span className="text-sm font-bold text-primary">{inTransitCount}</span>
                </div>
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-yellow-500" />
                    <span className="text-sm font-medium text-foreground">Scheduled</span>
                  </div>
                  <span className="text-sm font-bold text-yellow-600">{scheduledCount}</span>
                </div>
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-muted-foreground" />
                    <span className="text-sm font-medium text-foreground">Completed Today</span>
                  </div>
                  <span className="text-sm font-bold text-muted-foreground">3</span>
                </div>
              </div>
            </Card>
          </div>

          {/* Additional Stats */}
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">48.5 km</div>
              <div className="text-sm text-muted-foreground">Total Distance Today</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">156.2 kg</div>
              <div className="text-sm text-muted-foreground">Total Waste Transported</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">8</div>
              <div className="text-sm text-muted-foreground">Active Partners</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">98%</div>
              <div className="text-sm text-muted-foreground">On-Time Delivery Rate</div>
            </Card>
          </div>
        </div>
      </main>

      {/* Schedule Pickup Dialog */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>Schedule Pickup</DialogTitle>
            <DialogDescription>
              Enter waste details and schedule a pickup
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="grid grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label htmlFor="requesterId">Requester ID *</Label>
                <Input
                  id="requesterId"
                  placeholder="e.g., REQ-001"
                  value={formData.requesterId}
                  onChange={(e) => setFormData({ ...formData, requesterId: e.target.value })}
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="wasteType">Waste Type *</Label>
                <Input
                  id="wasteType"
                  placeholder="e.g., Coffee Grounds"
                  value={formData.wasteType}
                  onChange={(e) => setFormData({ ...formData, wasteType: e.target.value })}
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="quantity">Quantity (kg) *</Label>
                <Input
                  id="quantity"
                  type="number"
                  step="0.1"
                  min="0"
                  placeholder="e.g., 15.5"
                  value={formData.quantity}
                  onChange={(e) => setFormData({ ...formData, quantity: e.target.value })}
                  required
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="requesterName">Requester Name *</Label>
              <Input
                id="requesterName"
                placeholder="e.g., Hope Charity, Green Farm"
                value={formData.requesterName}
                onChange={(e) => setFormData({ ...formData, requesterName: e.target.value })}
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="status">Status *</Label>
              <Select
                value={formData.status}
                onValueChange={(value: "scheduled" | "delivered" | "pending") =>
                  setFormData({ ...formData, status: value })
                }
              >
                <SelectTrigger id="status">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="pending">Pending</SelectItem>
                  <SelectItem value="scheduled">Scheduled</SelectItem>
                  <SelectItem value="delivered">Delivered</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="pickupMethod">Pickup Method *</Label>
              <Select
                value={formData.pickupMethod}
                onValueChange={(value: "staff" | "courier") =>
                  setFormData({ ...formData, pickupMethod: value })
                }
              >
                <SelectTrigger id="pickupMethod">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="staff">Caf√© staff</SelectItem>
                  <SelectItem value="courier">External courier (Grab / Lalamove)</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="assignedTo">Staff or Courier name *</Label>
              <Input
                id="assignedTo"
                placeholder={formData.pickupMethod === "staff" ? "Staff member name" : "Courier name"}
                value={formData.assignedTo}
                onChange={(e) => setFormData({ ...formData, assignedTo: e.target.value })}
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="pickupDate">Pickup Date *</Label>
              <Input
                id="pickupDate"
                type="date"
                value={formData.pickupDate}
                onChange={(e) => setFormData({ ...formData, pickupDate: e.target.value })}
                min={new Date().toISOString().split("T")[0]}
                required
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label>Pickup Time From *</Label>
                <TimePicker
                  value={formData.pickupWindowFrom || "12:00"}
                  onChange={(value) => setFormData({ ...formData, pickupWindowFrom: value })}
                />
              </div>
              <div className="space-y-2">
                <Label>Pickup Time To *</Label>
                <TimePicker
                  value={formData.pickupWindowTo || "12:00"}
                  onChange={(value) => setFormData({ ...formData, pickupWindowTo: value })}
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="notes">Optional notes</Label>
              <Textarea
                id="notes"
                placeholder="Additional notes or instructions..."
                value={formData.notes}
                onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                rows={3}
              />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={handleCancel}>
              Cancel
            </Button>
            <Button onClick={handleSchedulePickup} className="bg-primary hover:bg-primary/90">
              <Truck className="w-4 h-4 mr-2" />
              Schedule Pickup
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

    </div>
  )
}

export default withAuth(TransportationPage, ["staff"])
